import getStyle from 'lib-bb-styles';
import {
  CSS_SELECTORS,
  CHART_OPTIONS,
 } from './constants';

const helpers = (context) => {
  const dateFilter = context.$filter('date');
  const i18nFilter = context.$filter('i18n');

  const productTypeTemplateNameMap = {
    'credit-card': 'credit-card',
    'debit-card': 'debit-card',
    'investment-account': 'investment',
    loan: 'loan',
    'current-account': 'current',
    'savings-account': 'savings',
    'term-deposit': 'term-deposit',
  };

  /**
   * @description
   * Generates account details template URL
   * @name getProductTypeTemplateId
   * @type {function}
   * @param  {string} type The account type
   * @return {string}      Generated template URL
   */
  const getProductTypeTemplateId = (type) => {
    const templateName =
      productTypeTemplateNameMap[type.trim().toLowerCase().split(' ').join('-')];
    return `account-details/${templateName}.html`;
  };

  /**
   * @description
   * Transforms a boolean into human readable format
   * @name formatBooleanToText
   * @type {function}
   * @param  {boolean} value The boolean value
   * @return {string}        Formatted booelan
   */
  const formatBooleanToText = (value) => (value ? 'yes' : 'no');

  /**
   * @description
   * Check if value is defined, not null and not empty
   * @name hasValue
   * @type {function}
   * @param  {*} value  The value to check
   * @return {boolean}
   */
  const hasValue = (value) => typeof value !== 'undefined' && value !== null && value !== '';

  const frequencyUnitKeyPrefix = 'accounts.detail.frequency';
  const frequencyUnits = {
    D: `${frequencyUnitKeyPrefix}.day`,
    W: `${frequencyUnitKeyPrefix}.week`,
    M: `${frequencyUnitKeyPrefix}.month`,
    Y: `${frequencyUnitKeyPrefix}.year`,
  };
  /**
   * @description
   * Gets the translation key giving a frequency unit identifier
   * @name getFrequencyUnitKey
   * @type {function}
   * @param  {string} unit  Short representation of frequency unit.
   *                        Possbile values are:
   *                        D - day
   *                        W - week
   *                        M - month
   *                        Y - year
   * @return {string} Translation key label
   */
  const getFrequencyUnitKey = (unit) => frequencyUnits[unit] || '';

  /**
   * @description
   * Checks if it is an array and it is empty
   * @name isEmptyArray
   * @type {function}
   * @param {*} value The value to check
   * @return {boolean}
   */
  const isEmptyArray = (value) => !(Array.isArray(value) && Boolean(value.length));

  /**
   * @description
   * Configuration object of balance history chart
   *
   * @inner
   * @name lineChartOptionsInner
   * @type {object}
   * @returns {object} Configuration object of performance chart
   */
  const lineChartOptionsInner = {
    legend: {
      display: false,
    },
    tooltips: {
      enabled: true,
      displayColors: false,
      callbacks: {
        title() {
        },
        label(tooltipItem) {
          return `(${tooltipItem.yLabel})  ${dateFilter(tooltipItem.xLabel, 'mediumDate')}`;
        },
      },
    },
    scales: {
      yAxes: [{
        gridLines: {
          display: true,
          zeroLineColor: getStyle(CSS_SELECTORS.yAxisZeroColor, 'color'),
          zeroLineWidth: CHART_OPTIONS.zeroLineWidth,
        },
      }],
      xAxes: [{
        ticks: {
          callback(label) {
            return `${dateFilter(label, 'short')}`;
          },
        },
      }],
    },
    layout: {
      padding: CHART_OPTIONS.paddings,
    },
    responsive: true,
    maintainAspectRatio: false,
  };

  /**
   * @description
   * Configuration of balance history chart
   *
   * @public
   * @name lineChartOptions
   * @type {function}
   * @returns {object} Configuration of performance chart
   */
  const lineChartOptions = ($ctrl) => {
    const currency = $ctrl.state.accounts.selected.currency;
    lineChartOptionsInner.tooltips.callbacks.label = (tooltipItem) =>
    // eslint-disable-next-line max-len
      `(${currency} ${tooltipItem.yLabel.toFixed(2)}) ${dateFilter(tooltipItem.xLabel, 'mediumDate')}`;
    return lineChartOptionsInner;
  };

  /**
   * @description
   * Array of plugins used to transform Chart.js rendering in the extension
   *
   * @name chartPlugins
   * @type {array}
   */
  const chartPlugins = [
    {
      afterInit: (chartInstance) => {
        setTimeout(() => {
          Object.assign(chartInstance.canvas.style, { height: CHART_OPTIONS.height });
          chartInstance.update();
          chartInstance.render(CHART_OPTIONS.animation_time, true);
          chartInstance.resize();
        }, 0);
      },
      beforeInit: (chartInstance) => {
        Object.assign(chartInstance.options, { maintainAspectRatio: false });
      },
    },
  ];

  /**
   * @description
   * Find max and min for given data series of balance history
   *
   * @public
   * @name getMaxMinLegend
   * @type {function}
   * @param {array} dataSeries Balance histiry data series
   * @return {Object} with max and min
   */
  const getMaxMinLegend = (dataSeries) => ({
    max: Math.max(
      ...dataSeries.map((item) => item.value.balance)
    ),
    min: Math.min(
      ...dataSeries.map((item) => item.value.balance)
    ),
  });

  /**
   * @description
   * Handles one or multiple errors messages, mapping error key with propper message
   *
   * @name handleErrors
   * @type {function}
   * @param {array} errors
   * @returns {string} message
   */
  const handleErrors = (errors) => {
    if (!errors) {
      return null;
    }
    return errors.map((e) => i18nFilter(e)).join(' ');
  };

  return {
    getProductTypeTemplateId,
    formatBooleanToText,
    hasValue,
    getFrequencyUnitKey,
    isEmptyArray,
    lineChartOptions,
    chartPlugins,
    getMaxMinLegend,
    handleErrors,
  };
};

export default helpers;



